<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process151"></a>

<p><br></p>
<p><span class="rvts17">主线：源力觉醒（</span><span class="rvts50">Powerful Awaking</span><span class="rvts17">）</span><span class="rvts50">.</span><span class="rvts17">上篇</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:441,Y:151</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：梅斯特湿婆（</span><span class="rvts20">Siva, The Meistr of Seekers</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">拯救梅斯特湿婆，并让她教我们源力法术</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1106.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">在浮木城东城门桥岸的小路旁，梅斯特湿婆正在接受行刑。据了解，湿婆由于触犯了源力使用的规则，被惩罚与此。必须拯救她，并让她指引更多的源力使用方法。</span>
<br><span class="rvts21">拯救湿婆最终将导致一场劫囚大战（可以尝试劝说魔导师，但根据目前等级几乎是无效的）。千万不要以为只看到了湿婆身旁的一名魔导师就以为在场的只有一名官差。其实，刑场周围埋伏了额外的</span><span class="rvts20">3</span><span class="rvts21">名魔导师和一只魔导犬。</span>
<br><span class="rvts21">大战之后，湿婆会被释放，并逃回浮木城的屋子里。强烈建议首先完成拯救湿婆的这段任务并获得第一个仪式的</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">技能，再进行浮木城以及其它额外内容的探索。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1107.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">接着和湿婆谈话，尽量使用客气的语气（毕竟人家是大师），她会告诉自己屋子里有一个地下密室。其实地下密室的开关按钮在浴池旁边，仔细寻找后可以竖起木床。</span>
<br><span class="rvts21">进入密室需要密码，这个密码是个很复杂的密码，只有湿婆一人知道。询问她密码后，便可以进入密室。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1108.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">据湿婆说，想要获得源力视野技能，需要进行特殊的仪式。首先需要在密室中的一个小储藏里（源力能量瓶旁边），找到仪式需要的任务道具（一个放血小刀，一个仪式碗，一些黑根植物）。</span>
<br><span class="rvts21">一定要先补充身上的源力值，再使用放血小刀（右键点击装备它），便会获得角色的血液。把染血的小刀与仪式碗相结合，合成一个仪式血液样本。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1109.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">之后把血液样本放入可以喷火的那个池子里，按下旁边的喷火阀门，可以使血液蒸发。进入绿色雾状区域，进行互动，就可以再次进入到回音大厅，和神对话后，便获得了</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">技能。</span>
<br><span class="rvts21">当想要让湿婆教会自己更多源力技能时，湿婆告知更高端的仪式需要首先获得更多的源力值（最大值），并且获得相应的材料，才可以继续进行。因此接下来需要完成</span><span class="rvts20"></span><span class="rvts21">获得额外源力点</span><span class="rvts20"></span><span class="rvts21">和</span><span class="rvts20"></span><span class="rvts21">获得黑根（</span><span class="rvts20">Blackroot</span><span class="rvts21">）任务物品</span><span class="rvts20"></span><span class="rvts21">。</span>
<br><span class="rvts21">最后的仪式详情请参考主线任务</span><span class="rvts20"></span><span class="rvts21">源力觉醒</span><span class="rvts20"></span><span class="rvts21">的下篇。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



